body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html{
  background-color: #3c3836;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  background-color: #ebdbb2;
  color: #282828;
  width: 30%;
  height: 30%;
  padding: 2%;
  text-align: center;
  text-decoration: none;
  font-size: 6vh;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  border: 1px solid #3c3836;
  border-radius: 10px;
  order: 1;
}

h1{
  text-align: center;
  margin-bottom: 0;
  text-shadow: 1px 1px #fe8019;
}

p{
  text-align: center;
  font-size: 4vh;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
}

.title{
  color: #ebdbb2;
  margin-bottom: 0;
  text-shadow: 2px 2px #458588;
}

button:hover{
  background-color: #fabd2f;
}

button:focus {
  outline: 2px solid #fabd2f;
}


.buttonBackground {
  background-color: #3c3836;
  display: flex;
  margin: 0 auto;
  height: 80vh;
  min-width: 440px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.operator{
  background-color: #7c6f64;
}

.input{
  background-color: #458588;
  display: flex;
  margin: 0 auto;
  width: 100%;
  height: 15vh;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  border-bottom: 5px solid #fabd2f;
}

.setBackground{
  border: 3px solid #fe8019;
  border-radius: 15px;
  background-color: #8ec07c;
  display: flex;
  flex-grow: 1;
  flex-basis: 33%;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  margin: 1% auto;
  width: 90%;
  min-width: 90px;
  max-width: 450px;
  font-size: 6vw;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
}

.timerBackground{
  border: 3px solid #fe8019;
  border-radius: 15px;
  background-color: #8ec07c;
  display: flex;
  flex-grow: 1;
  justify-content: space-evenly;
  align-items: center;
  flex-flow: row wrap;
  margin: 1% auto;
  padding: 0 0 4% 0;
  width: 90%;
  min-width: 440px;
  font-size: 6vw;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
}

.timerDisplay{
  order: 1;
  flex-basis: 100%;
}

.sessionButton{
  order: 4;
  justify-self: center;
}